import React from 'react';
import { BaseCommand } from "./base.js"
import {
    Grid, TextField, Button,
} from "@material-ui/core"

class Password extends BaseCommand {
    constructor(props) {
        super(props);
        this.state = {
            newPassword: "",
            confirmPassword: "",
        };
    }

    createCode = () => {
        if (this.state.newPassword === "" || this.state.confirmPassword === "") {
            alert("New password fields cannot be empty")
            return null;
        }

        if (this.state.newPassword !== this.state.confirmPassword) {
            alert("Passwords do not match")
            return null;
        }

        if (this.state.newPassword.length >= 8) {
            let command = this.generateCommand(this.state.newPassword)
            this.setState({ code: command })
        } else {
            alert("Password needs to be minimum of 8 characters")
        }
    }

    render() {
        return (
            <div>
                <Grid container spacing={3} direction={"column"}>
                    <Grid item>
                        <TextField name='password'
                            value={this.state.password || ''}
                            label="Password"
                            onChange={(e) => this.onChange(e, true, 50)} />
                    </Grid>
                    <Grid item>
                        <TextField name='newPassword'
                            value={this.state.newPassword || ''}
                            label="New Password"
                            onChange={(e) => this.onChange(e, true, 50)} />
                    </Grid>
                    <Grid item>
                        <TextField name='confirmPassword'
                            value={this.state.confirmPassword || ''}
                            label="Confirm Password"
                            onChange={(e) => this.onChange(e, true, 50)} />
                    </Grid>
                    <Grid item>
                        <Button variant="contained" onClick={this.createCode}>Generate Code</Button>
                    </Grid>
                    <Grid>
                        {this.displayQRCode()}
                    </Grid>
                </Grid>
            </div>
        )
    }
}

export default Password