/**
 * A Base class for every command in this system
 * TODO: Document
 */
import React from 'react';
import  { validateTextField } from "../constants.js"

var md5 = require("blueimp-md5")
var QRCode = require('qrcode.react');

const MSG_VERSION = 1

export const setProp = (prop, fallback) => {
    return prop ? prop : fallback
}

export class BaseCommand extends React.Component {

    constructor(props) {
        super(props)
        this.state = {
            code: "",
            showPassword: false,
            password: "",
        }
    }

    generateCommand = (value) => {
        if (!this.state.password || this.state.password.length < 8) {
            alert("Password must be at least 8 characters long")
            return null
        }
        var command = {
            "N": MSG_VERSION,
            "T": this.props.commandType,
            "V": value
        }
        command = JSON.stringify(command)
        console.log(command)

        let secret = this.state.password + this.state.password

        // Set the parameter to True to get the raw string
        let signature = btoa(md5(command, secret, true))
        command = `{P}|{${signature}}|${command}`
        console.log(command)
        return command
    }

    // Use this for any generic text field
    onChange = (e, validate = true, maxLength = 50) => {
        var value = e.target.value
        if (validate) value = validateTextField(value, true, maxLength)
        this.setState({ [e.target.name]: value, code: "" })
    }

    displayQRCode = _ => {
        if (this.state.code && this.state.code.length > 1) {
            return (<div><QRCode value={this.state.code} size={Math.min(this.props.width * 0.90, 480)} /></div>)
        }
    }
}