import React from 'react';
import {
    Grid, FormControl, InputLabel,
    MenuItem, Select
} from "@material-ui/core"

// Custom Command Components
import Mapping from "./commands/mapping"
import Option from "./commands/option"
import Password from "./commands/password"

import { commands, wifiOptions, systemOptions, bluetoothOptions, pins } from "./constants.js"


export default class QlickyAdmin extends React.Component {
    constructor(props) {
        super(props);
        this.resize = this.resize.bind(this)
        this.state = {
            commandType: "DRY",
        };
    }

    componentDidMount() {
        window.addEventListener('resize', this.resize)
        this.resize()
    }

    resize() {
        this.setState({ width: window.innerWidth, height: window.innerHeight });
    }

    onChange = (e) => {
        console.log(e.target.value)
        this.setState({ [e.target.name]: e.target.value })
    }

    renderCommand = () => {
        switch (this.state.commandType) {
            case "PASS":
                return (<Password commandType={this.state.commandType} width={this.state.width} key={"PASS"} />)
            case "DRY":
                return (<Mapping commandType={this.state.commandType} options={pins} width={this.state.width} key={"DRY"} />)
            case "DIR":
                return (<Mapping commandType={this.state.commandType} freeRow={true} options={pins} width={this.state.width} key={"DIR"} />)
            case "WIFI":
                return (<Option commandType={this.state.commandType} options={wifiOptions} width={this.state.width} key={"WIFI"} />)
            case "BLE":
                return (<Option commandType={this.state.commandType} options={bluetoothOptions} width={this.state.width} key={"BLE"} />)
            case "SYS":
                return (<Option commandType={this.state.commandType} options={systemOptions} width={this.state.width} key={"SYS"} />)
            default:
                return null
        }
    }

    render() {
        if (isNaN(this.state.width) || isNaN(this.state.height)) { return (<div />) }
        return (
            <div>
                <Grid container spacing={3} direction={"column"}>
                    <Grid item> <h2> Qlicky Reprogram Tool </h2> </Grid>
                    <Grid item>
                        <FormControl style={{ minWidth: 120 }}>
                            <InputLabel>Command</InputLabel>
                            <Select
                                name="commandType"
                                value={this.state.commandType}
                                onChange={this.onChange}
                            >
                                {commands.map((option, index) => {
                                    return (
                                        <MenuItem value={option.value} key={`${index}`}>{option.label}</MenuItem>
                                    )
                                })}
                            </Select>
                        </FormControl>
                    </Grid>
                    <Grid item>
                        {this.renderCommand()}
                    </Grid>
                </Grid>
            </div>
        )
    }
}