/*
    A Shared UI Config between all the 
    SINGLE option based CommandTypes 
    Example: BLE, SYS, WIFI
*/
import React from 'react';
import {
    Grid, TextField, Button,
    FormControl, InputLabel, MenuItem, Select
} from "@material-ui/core"
import { BaseCommand } from './base.js';

export default class Option extends BaseCommand {
    constructor(props) {
        super(props);
        this.state = {
            selectedOption: "",
        };
    }

    createCode = () => {
        if (this.state.selectedOption.length === 0) {
            alert("Empty Key-Value map. Cannot generate QR Code")
            return null
        }

        if (this.state.selectedOption) {
            let command = this.generateCommand(this.state.selectedOption)
            this.setState({ code: command })
        }
    }

    render() {
        return (
            <div>
                <Grid container spacing={2} direction={"column"}>
                    <Grid item>
                        <TextField name='password'
                            value={this.state.password || ''}
                            label="Password"
                            onChange={this.onChange} />
                    </Grid>
                    <Grid item>
                        <FormControl style={{ minWidth: 120 }}>
                            <InputLabel>Pin No</InputLabel>
                            <Select
                                name="selectedOption"
                                value={this.state.selectedOption}
                                onChange={this.onChange}
                            >
                                {this.props.options.map((option, index) => {
                                    return (
                                        <MenuItem value={option.value} key={`${index}`}>{option.label}</MenuItem>
                                    )
                                })}
                            </Select>
                        </FormControl>
                    </Grid>
                    <Grid item>
                        <Button variant="contained" onClick={this.createCode}>Generate Code</Button>
                    </Grid>
                    <Grid>
                        {this.displayQRCode()}
                    </Grid>
                </Grid>
            </div>
        )
    }
}
