import React from 'react';
import { Grid, TextField, Button } from "@material-ui/core"
import QlickyAdmin from "./qlicky.js"

export default class Main extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            password: "",
            passwordAccepted: false,
        };
    }

    verifyPassword = _ => {
        if (this.state.password && this.state.password === 'touchless') {
            this.setState({ passwordAccepted: true })
        } else {
            alert("Incorrect Password!")
        }
    }

    createReprogramTool = _ => {
        return (
            <div><QlickyAdmin /></div>
        )
    }

    createPasswordScreen = _ => {
        return (
            <div>
                <Grid container spacing={3} direction={"column"}>
                    <Grid item> <h2> Qlicky Reprogram Tool </h2> </Grid>
                    <Grid item> Please enter password to continue </Grid>
                    <Grid item>
                        <TextField
                            type="text"
                            name="password"
                            value={this.state.password}
                            label="Password"
                            onChange={(e) => {
                                this.setState({ [e.target.name]: e.target.value })
                            }}
                        />
                    </Grid>
                    <Grid item>
                        <Button
                            variant="contained"
                            onClick={this.verifyPassword}>Open Tool</Button>
                    </Grid>
                </Grid>
            </div>
        )
    }

    render() {
        return (
            <div>
                {this.state.passwordAccepted ? this.createReprogramTool() : this.createPasswordScreen()}
            </div>
        )
    }
}