/**
 * Shared Class between all mapping based
 * commands. 
 * Example: DIR, DRY
 */

import React from 'react';
import { validateTextField } from "../constants.js"
import {
    Grid, TextField, Button,
    FormControl, InputLabel, MenuItem, Select
} from "@material-ui/core"

import { BaseCommand } from "./base"

const MAX_ROWS = 16

export default class Mapping extends BaseCommand {
    constructor(props) {
        super(props);
        this.mapping = []
        this.state = {
            mapping: [],
        };
    }

    // Key =  Floor/Directory 
    // Value = Pin
    componentDidMount() {
        this.mapping = []
        this.mapping.push({ "value": "", "key": "" })
        this.setState({ mapping: this.mapping })
    }

    removeDuplicates = (remove = false) => {
        // Use dict for O(1) search
        var floorMemo = {}
        var pinMemo = {}
        var duplicatePins = {}
        var duplicateFloors = {}

        this.mapping.forEach(function (mapping, index) {
            if (mapping.key) {
                if (mapping.key in floorMemo) duplicateFloors[index] = true
                else floorMemo[mapping.key] = true;
            }
            if (mapping.value) {
                if (mapping.value in pinMemo) duplicatePins[index] = true
                else pinMemo[mapping.value] = true
            }
        })

        if (Object.keys(duplicatePins).length > 0 || Object.keys(duplicateFloors).length > 0) {
            alert("WARNING: Duplicate entries found. They will be removed before QRCode generation")
        }
        if (remove) {
            var newMapping = []
            this.mapping.forEach((mapping, index) => {
                if (!(index in duplicateFloors || index in duplicatePins))
                    newMapping.push(mapping)
            })
            this.mapping = newMapping;
            this.setState({ mapping: newMapping })
        }
        return;
    }

    createCode = () => {
        if (this.state.mapping.length === 0) {
            alert("Empty Key-Value map. Cannot generate QR Code")
            return null
        }
        this.removeDuplicates(true)
        var mappings = ""
        this.state.mapping.forEach(function (mapping, _) {
            if (mapping["key"] !== "" && mapping["value"] !== "")
                mappings = `${mappings}"${mapping.key}":${mapping.value},`
        })
        if (mappings.length > 0) {
            mappings = `{${mappings.slice(0, -1)}}`
            console.log(`Mappings: ${JSON.stringify(mappings)}`)
            let command = this.generateCommand(mappings)
            if (command) {
                this.setState({ code: command })
            }
        } else {
            alert("Empty mapping set")
        }
    }

    editRow = (_, addRow) => {
        if (addRow) {
            if (this.state.mapping.length >= MAX_ROWS) {
                alert(`A maximimum of ${MAX_ROWS} allowed per QR Code`)
            } else {
                this.mapping.push({ "value": "", "key": "" })
                this.setState({ mapping: this.mapping })
            }
        } else {
            if (this.state.mapping.length > 1) {
                this.mapping.pop()
                this.setState({ mapping: this.mapping })
            }
        }
    }

    updateMap = (e, name, index, maxCharacters = 20, validate = false) => {
        var value = e.target.value;
        if (validate) value = validateTextField(value, true, maxCharacters)
        this.mapping[index][name] = value
        this.setState({ mapping: this.mapping, code: "" })
    }

    makeOptionRow = (row, index) => {
        if (!row) return
        return (
            <Grid container spacing={5} direction={"row"} justify="center" alignItems="center" key={`${index}`}>
                <Grid item>
                    <TextField
                        name={`${index}`}
                        value={this.state.mapping[index].key}
                        label="Floor No"
                        onChange={(e) => this.updateMap(e, "key", index, 20, true)}
                    />
                </Grid>
                <Grid item>
                    <FormControl style={{ minWidth: 120 }}>
                        <InputLabel>Pin No</InputLabel>
                        <Select
                            name={`${index}`}
                            value={this.state.mapping[index].value}
                            onChange={(e) => this.updateMap(e, "value", index)}
                        >
                            {this.props.options.map((option, index) => {
                                return (
                                    <MenuItem value={option.value} key={`${index}`}>{option.label}</MenuItem>
                                )
                            })}
                        </Select>
                    </FormControl>
                </Grid>
            </Grid >
        )
    }

    makeFreeTextRow = (row, index) => {
        if (!row) return
        return (
            <Grid container spacing={5} direction={"row"} justify="center" alignItems="center" key={`${index}`}>
                <Grid item>
                    <TextField
                        name={`key-${index}`}
                        value={this.state.mapping[index].key}
                        label="Keyword"
                        onChange={(e) => this.updateMap(e, "key", index, 50)}
                    />
                </Grid>
                <Grid item>
                    <TextField
                        name={`value-${index}`}
                        value={this.state.mapping[index].value}
                        label="Floor No"
                        onChange={(e) => this.updateMap(e, "value", index, 20)}
                    />
                </Grid>
            </Grid >
        )
    }

    render() {
        return (
            <div>
                <Grid container spacing={3} direction={"column"}>
                    <Grid item>
                        <TextField name='password'
                            value={this.state.password || ''}
                            label="Password"
                            onChange={this.onChange} />
                    </Grid>
                    <Grid item>
                        {this.state.mapping.map((row, index) => {
                            return this.props.freeRow ?
                                this.makeFreeTextRow(row, index) :
                                this.makeOptionRow(row, index)
                        })}
                    </Grid>
                    <Grid container spacing={3} direction="row" justify="center" alignItems="center">
                        <Grid item>
                            <Button variant="contained"
                                onClick={(e) => this.editRow(e, true)}> Add </Button>
                        </Grid>
                        <Grid item>
                            <Button variant="contained"
                                onClick={(e) => this.editRow(e, false)}> Remove </Button>
                        </Grid>
                    </Grid>
                    <Grid item>
                        <Button variant="contained" onClick={this.createCode}> Generate Code </Button>
                    </Grid>
                    <Grid item>
                        {this.displayQRCode()}
                    </Grid>
                </Grid>
            </div>
        )
    }
}